import React, { useEffect } from 'react';

import { fonts } from '../constants';
import { identifySegment, trackSegment } from '../utils/segment';
import { getCampaign } from '../utils/utm';

export default function BlockHubspotForm() {
  useEffect(() => {
    const script = document.createElement('script');
    script.src = '//js.hsforms.net/forms/v2.js';
    document.body.appendChild(script);

    window.jQuery =
      window.jQuery ||
      function (nodeOrSelector) {
        if (typeof nodeOrSelector === 'string') {
          return document.querySelector(nodeOrSelector);
        }
        return nodeOrSelector;
      };

    script.addEventListener('load', () => {
      if (window.hbspt) {
        window.hbspt.forms.create({
          portalId: '8982602',
          formId: '00761d29-60f5-4b4c-afd9-0882c320d2f5',
          target: '#hubspotForm',
          onFormSubmit: function ($form) {
            const formData = new FormData($form);

            const name = `${formData.get('firstname')} ${formData.get(
              'lastname'
            )}`;
            const email = formData.get('email');
            const company = formData.get('company');
            const phone = formData.get('phone');
            const job_title = formData.get('jobtitle');
            const website = formData.get('website');
            const industry = formData.get('company_industry');
            const customer_purchase_frequency = formData.get(
              'customer_purchase_frequency'
            );
            const annual_revenue = formData.get('annual_b2b_revenue');
            const offer_net_terms = formData.get('do_you_offer_net_terms_');
            const accounting_platform = formData.get('accounting_platform');
            const ecommerce_platform = formData.get('e_commerce_platform');

            identifySegment({
              email,
              phone,
              name,
              company: { name: company },
              website,
              job_title,
              industry,
              annual_revenue,
              accounting_platform,
              customer_purchase_frequency,
              offer_net_terms,
              ecommerce_platform,
            });
            trackSegment('Contact form submitted', {
              name,
              email,
              phone,
              company,
              website,
              context: {
                campaign: getCampaign(),
              },
              job_title,
              industry,
              annual_revenue,
              accounting_platform,
              customer_purchase_frequency,
              offer_net_terms,
              ecommerce_platform,
            });
          },
        });
      }
    });
  }, []);

  return (
    <div>
      <p className="contact_sales__subtitle">
        Tell us how to reach you and let's discuss how Resolve can grow your b2b
        business.
      </p>
      <div className="contact_sales_form__form-container">
        <div id="hubspotForm"></div>
      </div>
      <style jsx>{`
        .clearfix::after {
          content: '';
          clear: both;
          display: table;
        }
        .contact_sales__subtitle {
          font-family: ${fonts.halyardDisplay};
          font-size: 22px;
          line-height: 1.64;
          letter-spacing: 0.8px;
          margin: 20px 0 40px;
          text-align: center;
        }
      `}</style>
      <style jsx global>{`
        .contact_sales__container * {
          box-sizing: border-box;
        }
      `}</style>
    </div>
  );
}
